// Ð½Ð°Ð·Ð²Ð° ÐºÐ»ÑŽÑ‡Ð° Ð´Ð»Ñ localStorage Ñ‚Ð° Ð·Ð¼Ñ–Ð½Ð½Ð° Ñ‰Ð¾ Ð·Ð±ÐµÑ€Ñ–Ð³Ð°Ñ” Ð¾Ð±Ñ”ÐºÑ‚ Ð¿Ð¾Ñ‚Ð¾Ñ‡Ð½Ð¾Ñ— ÐºÐ½Ð¸Ð³Ð¸
const STORAGE_KEY = 'bookList';
// Ð·Ð¼Ñ–Ð½Ð½Ð° Ñ‰Ð¾ Ð¿Ñ€Ð¸Ð¹Ð¼Ð°Ñ” Ð¾Ð±Ñ”ÐºÑ‚Ð¸ Ð¿Ð¾Ñ‚Ð¾Ñ‡Ð½Ð¾Ñ— ÐºÐ½Ð¸Ð³Ð¸
let newBook = {};
// Ð·Ð¼Ñ–Ð½Ð½Ð°, Ñ‰Ð¾ Ð¼Ñ–ÑÑ‚Ð¸Ñ‚ÑŒ Ð¿Ð¾ÑÐ¸Ð»Ð°Ð½Ð½Ñ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð´Ð¾Ð´Ð°Ñ‚Ð¸(Ð²Ð¸Ð´Ð°Ð»Ð¸Ñ‚Ð¸) ÐºÐ½Ð¸Ð³Ñƒ
const btnAddRemove = refs.popupEl.lastElementChild.previousElementSibling;

// ÑÐ»ÑƒÑ…Ð°Ñ‡ Ð´Ð»Ñ Ð²Ð¸ÐºÐ»Ð¸ÐºÑƒ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð°
refs.booksPart.addEventListener('click', callPopupWindow);

// ÑÐ»ÑƒÑ…Ð°Ñ‡Ñ– Ð´Ð»Ñ Ð²Ð¸ÐºÐ»Ð¸ÐºÑƒ Ñ„ÑƒÐ½ÐºÑ†Ñ–Ñ—, Ñ‰Ð¾ Ð·Ð°ÐºÑ€Ð¸Ð²Ð°Ñ” Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ðµ Ð²Ñ–ÐºÐ½Ð¾
// ÑÐ»ÑƒÑ…Ð°Ñ‡ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð·Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ
refs.popupEl.firstElementChild.addEventListener('click', OffFunction);
// ÑÐ»ÑƒÑ…Ð°Ñ‡ Ð½Ð° Ð±ÐµÐºÐ´Ñ€Ð¾Ð¿
refs.popupEl.parentNode.addEventListener('click', OffFunction);
// Ð¡Ð»ÑƒÑ…Ð°Ñ‡ Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ ESC
document.addEventListener('keydown', OffFunction);

// Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð·Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð° Ð´Ð»Ñ Ð±ÐµÐºÐ´Ñ€Ð¾Ð¿Ñƒ Ñ‚Ð° ÐºÐ½Ð¾Ð¿ÐºÐ¸ ESC
function OffFunction(evt) {
  if (
    //ÐŸÐµÑ€ÐµÐ²Ñ–Ñ€ÐºÐ° Ñ‚Ð¾Ð³Ð¾ Ð¿Ð¾Ð´Ñ–Ñ Ð²Ð¸ÐºÐ»Ð¸ÐºÐ°Ð½Ð° ÑÐ°Ð¼Ðµ Ð½Ð° Ð±ÐµÐºÐ´Ñ€Ð¾Ð¿Ñ–
    evt.target === evt.currentTarget ||
    //ÐŸÐµÑ€ÐµÐ²Ñ–Ñ€ÐºÐ° Ñ‚Ð¾Ð³Ð¾, Ñ‰Ð¾ Ð½Ð°Ñ‚Ð¸ÑÐ½ÑƒÑ‚Ð° ÑÐ°Ð¼Ðµ ÐºÐ»Ð°Ð²Ñ–ÑˆÐ° ESC
    evt.key === 'Escape' ||
    //ÐŸÐµÑ€ÐµÐ²Ñ–Ñ€ÐºÐ° Ñ‚Ð¾Ð³Ð¾, Ñ‰Ð¾ ÐºÐ»Ñ–Ðº Ð²Ñ–Ð´Ð±ÑƒÐ²ÑÑ Ð°Ð±Ð¾ Ð¿Ð¾ ÐºÐ½Ð¾Ð¿Ñ†Ñ– Ð·Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ, Ð°Ð±Ð¾ Ð¿Ð¾ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñƒ Ð²ÑÐµÑ€ÐµÐ´ÐµÐ½Ñ–
    evt.target.closest('.popup-btn-close')
  ) {
    // Ð”Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ Ð´Ð¾ Ð±ÐµÐºÐ´Ñ€Ð¾Ð¿Ñƒ ÑÑ‚Ð¸Ð»ÑŽ "is-hidden"
    refs.popupEl.parentNode.classList.add('is-hidden');
    // Ð²Ð¸Ð´Ð°Ð»ÐµÐ½Ð½Ñ Ð· Ð±Ð¾Ð´Ñ– ÑÑ‚Ð¸Ð»ÑŽ Ð´Ð»Ñ Ð·Ð°Ð¼Ð¾Ñ€Ð¾Ð·ÐºÐ¸ ÑÐºÑ€Ð¾Ð»Ñƒ Ð½Ð° Ñ„Ð¾Ð½Ñ– Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð°
    refs.body.classList.remove('popup-modal-open');
    // Ð¾Ñ‡Ð¸ÑÑ‚ÐºÐ° Ñ€Ð¾Ð·Ð¼Ñ–Ñ‚ÐºÐ¸ ÐºÐ°Ñ€Ñ‚ÐºÐ¸ Ð· ÐºÐ½Ð¸Ð³Ð¾ÑŽ, Ñ‰Ð¾Ð± Ð¿Ð¾Ð¿ÐµÑ€ÐµÐ´Ð½ÑŒÐ¾ Ð²Ñ–Ð´ÐºÑ€Ð¸Ñ‚Ð° ÐºÐ½Ð¸Ð³Ð° Ð½Ðµ Ð·ÑÐ²Ð»ÑÐ»Ð°ÑÑ Ð½Ð° Ð¼Ð¸Ñ‚ÑŒ Ð¿ÐµÑ€ÐµÐ´ Ð½Ð¾Ð²Ð¾ÑŽ
    refs.popupEl.firstElementChild.nextElementSibling.innerHTML = '';
  }
}
// Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð²Ð¸ÐºÐ»Ð¸ÐºÑƒ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð²Ñ–ÐºÐ½Ð°
function callPopupWindow(evt) {
  // Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ‡ÐºÐ° Ñ‚Ð¾Ð³Ð¾ Ñ‡Ð¸ ÐºÐ»Ñ–Ðº ÑÐ¿Ñ€Ð°Ð²Ð´Ñ– Ð²Ð²Ñ–Ð´Ð±ÑƒÐ²ÑÑ Ð¿Ð¾ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñƒ li
  if (!evt.target.closest('.book-cards-list-item')) {
    return;
  }
  // Ð Ð¾Ð±Ð¸Ñ‚ÑŒ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ðµ Ð²Ñ–ÐºÐ½Ð¾ Ð½Ðµ Ð¿Ñ€Ð¸Ñ…Ð¾Ð²Ð°Ð½Ð¸Ð¼
  refs.popupEl.parentNode.classList.toggle('is-hidden');
  // Ð·Ð°Ð¼Ð¾Ñ€Ð¾Ð¶ÑƒÑ” ÑÐºÑ€Ð¾Ð» Ð½Ð° Ð±ÐµÐºÐ´Ñ€Ð¾Ð¿
  refs.body.classList.toggle('popup-modal-open');
  // Ð¾Ñ‚Ñ€Ð¸Ð¼ÑƒÑ” id ÐºÐ½Ð¸Ð³Ð¸
  const id = evt.target.closest('LI').dataset.bookId;
  // Ð’Ð¸ÐºÐ»Ð¸Ðº Ñ„ÑƒÐ½ÐºÑ†Ñ–Ñ—, Ñ‰Ð¾ Ñ€Ð¾Ð±Ð¸Ñ‚ÑŒ Ð·Ð°Ð¿Ð¸Ñ‚ Ð½Ð° Ð±ÐµÐºÐµÐ½Ð´
  getBooksId(id)
    .then(data => {
      newBook = data;
      // Ð’Ð¸ÐºÐ»Ð¸Ðº Ñ„ÑƒÐ½ÐºÑ†Ñ–Ñ— Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€ÐºÐ¸ Ð½Ð°ÑÐ²Ð½Ð¾ÑÑ‚Ñ– Ð´Ð°Ð½Ð¾Ñ— ÐºÐ½Ð¸Ð³Ð¸ Ð² Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ð¾Ð¼Ñƒ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ñ–
      checkingBookList(newBook);
      let markup = createMarkupForPopup(newBook);
      // Ð´Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ Ñ€Ñ€Ð¾Ð·Ð¼Ñ–Ñ‚ÐºÐ¸
      refs.popupEl.firstElementChild.nextElementSibling.innerHTML = markup;
    })
    .catch(err => {
      console.error(err);
      // Notify.failure('Oops! Something went wrong! Try reloading the page!');
    })
    .finally(_ => (refs.loaderForAllCategories.style.display = 'none'));
}

// Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€ÐºÐ¸ Ð½Ð°ÑÐ²Ð½Ð¾ÑÑ‚Ñ– Ð¾Ð±Ñ”ÐºÑ‚Ð° ÐºÐ½Ð¸Ð³Ð¸ Ð² Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ð¾Ð¼Ñƒ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ñ–
async function checkingBookList(data) {
  try {
    // Ð¾Ñ‚Ñ€Ð¸Ð¼Ð°Ð½Ð½Ñ Ð¼Ð°ÑÐ¸Ð²Ñƒ Ð¾Ð±Ñ”ÐºÑ‚Ñ–Ð² ÐºÐ½Ð¸Ð³, Ñ‰Ð¾ Ð²Ð¶Ðµ Ð´Ð¾Ð´Ð°Ð½Ñ– Ð´Ð¾ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ð°
    const arr = await getDataFromStorage(STORAGE_KEY)
      .then(data => {
        return data ? data : [];
      })
      .catch(error => {
        console.log(error.message);
        // Notify.failure('Oops! Something went wrong! Try reloading the page!');
      });
    // Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€ÐºÐ° Ð¼ÐµÑ‚Ð¾Ð´Ð¾Ð¼ some Ñ‡Ð¸ Ñ” ÑÐµÑ€ÐµÐ´ Ð¼Ð°ÑÐ¸Ð²Ñƒ Ð¾Ð±Ñ”ÐºÑ‚Ñ–Ð² ÐºÐ½Ð¸Ð³, Ð¾Ð±Ñ”ÐºÑ‚ Ð· Ñ‚Ð°ÐºÐ¸Ð¼ Ð¶Ðµ id ÑÐº id Ð¿Ð¾Ñ‚Ð¾Ñ‡Ð½Ð¾Ñ— ÐºÐ½Ð¸Ð³Ð¸
    const status = arr.some(({ _id }) => _id === data._id);
    if (status) {
      // Ð² Ñ€Ð°Ð·Ñ– Ð¿Ñ–Ð´Ñ‚Ð²ÐµÑ€Ð´Ð¶ÐµÐ½Ð½Ñ: Ð²Ð¸Ð´Ð°Ð»ÑÑ”Ñ‚ÑŒÑÑ ÑÐ»ÑƒÑ…Ð°Ñ‡ Ð· Ñ„ÑƒÐ½ÐºÑ†Ñ–Ñ”ÑŽ Ð´Ð°Ð´Ð°Ð²Ð°Ð½Ð½Ñ ÐºÐ½Ð¸Ð³Ð¸
      btnAddRemove.removeEventListener('click', addBookToStorage);
      // Ð´Ð¾Ð´Ð°Ñ”Ñ‚ÑŒÑÑ ÑÐ»ÑƒÑ…Ð°Ñ‡ Ð· Ñ„ÑƒÐ½ÐºÑ†Ñ–Ñ”ÑŽ Ð²Ð¸Ð´Ð°Ð»ÐµÐ½Ð½Ñ ÐºÐ½Ð¸Ð³Ð¸
      btnAddRemove.addEventListener('click', removeBookFromStorage);
      // Ð·Ð¼Ñ–Ð½ÑŽÑ”Ñ‚ÑŒÑÑ Ð½Ð°Ð·Ð²Ð° ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð²Ð¸Ð´Ð°Ð»ÐµÐ½Ð½Ñ
      btnAddRemove.textContent = 'remove from the shopping list';
      // ÐŸÐ¾ÐºÐ°Ð·ÑƒÑ”Ñ‚ÑŒÑÑ Ð¿Ð¾Ð²Ñ–Ð´Ð¾Ð¼Ð»ÐµÐ½Ð½Ñ Ð¿Ñ€Ð¾ Ð½Ð°ÑÐ²Ð½Ñ–ÑÑ‚ÑŒ ÐºÐ½Ð¸Ð³Ð¸ Ð² ÑÐ¿Ð¸ÑÐºÑƒ Ð´Ð¾Ð´Ð°Ð½Ð¸Ñ…
      refs.popupEl.lastElementChild.hidden = false;
    } else {
      // Ð² Ñ€Ð°Ð·Ñ– Ð¿Ñ–Ð´Ñ‚Ð²ÐµÑ€Ð´Ð¶ÐµÐ½Ð½Ñ: Ð²Ð¸Ð´Ð°Ð»ÑÑ”Ñ‚ÑŒÑÑ ÑÐ»ÑƒÑ…Ð°Ñ‡ Ð· Ñ„ÑƒÐ½ÐºÑ†Ñ–Ñ”ÑŽ Ð²Ð¸Ð´Ð°Ð»ÐµÐ½Ð½Ñ ÐºÐ½Ð¸Ð³Ð¸
      btnAddRemove.removeEventListener('click', removeBookFromStorage);
      // Ð´Ð¾Ð´Ð°Ñ”Ñ‚ÑŒÑÑ ÑÐ»ÑƒÑ…Ð°Ñ‡ Ð· Ñ„ÑƒÐ½ÐºÑ†Ñ–Ñ”ÑŽ Ð´Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ ÐºÐ½Ð¸Ð³Ð¸
      btnAddRemove.addEventListener('click', addBookToStorage);
      // Ð·Ð¼Ñ–Ð½ÑŽÑ”Ñ‚ÑŒÑÑ Ð½Ð°Ð·Ð²Ð° ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð´Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ
      btnAddRemove.textContent = 'Add to shopping list';
      // ÐŸÑ€Ð¸Ñ…Ð¾Ð²ÑƒÑ”Ñ‚ÑŒÑÑ Ð¿Ð¾Ð²Ñ–Ð´Ð¾Ð¼Ð»ÐµÐ½Ð½Ñ Ð¿Ñ€Ð¾ Ð½Ð°ÑÐ²Ð½Ñ–ÑÑ‚ÑŒ ÐºÐ½Ð¸Ð³Ð¸ Ð² ÑÐ¿Ð¸ÑÐºÑƒ Ð´Ð¾Ð´Ð°Ð½Ð¸Ñ…
      refs.popupEl.lastElementChild.hidden = true;
    }
  } catch (error) {
    console.log(error.message);
    // Notify.failure('Oops! Something went wrong! Try reloading the page!');
  }
}

// Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð´Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ Ð¾Ð±Ñ”ÐºÑ‚Ð° ÐºÐ½Ð¸Ð³Ð¸ Ð´Ð¾ Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ð¾Ð³Ð¾ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ð°
async function addBookToStorage() {
  try {
    // Ð¾Ñ‚Ñ€Ð¸Ð¼Ð°Ð½Ð½Ñ Ð¼Ð°ÑÐ¸Ð²Ñƒ Ð¾Ð±Ñ”ÐºÑ‚Ñ–Ð² ÐºÐ½Ð¸Ð³, Ñ‰Ð¾ Ð²Ð¶Ðµ Ð·Ð±ÐµÑ€Ñ–Ð³Ð°Ñ”Ñ‚ÑŒÑÑ Ð² Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ð¾Ð¼Ñƒ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ñ–
    const dataFromStorage = await getDataFromStorage(STORAGE_KEY)
      .then(data => {
        return data ? data : [];
      })
      .catch(error => {
        console.log(error.message);
        // Notify.failure('Oops! Something went wrong! Try reloading the page!');
      });
    // Ð´Ð¾Ð´Ð°Ð²Ð°Ð½Ð½Ñ Ð´Ð¾ Ð¼Ð°ÑÐ¸Ð²Ñƒ ÐºÐ½Ð¸Ð³ Ñƒ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ñ–, Ñ‰Ðµ Ð¾Ð´Ð½Ð¾Ð³Ð¾ Ð¾Ð±Ñ”ÐºÑ‚Ñƒ Ð· Ð½Ð¾Ð²Ð¾ÑŽ ÐºÐ½Ð¸Ð³Ð¾ÑŽ
    dataFromStorage.push(newBook);
    // Ð¿Ð¾Ð¼Ñ–Ñ‰Ð°Ñ”Ð¼Ð¾ Ð½Ð¾Ð²Ð¸Ð¹ Ð¼Ð°Ð¸ÑÐ², Ñ‰Ð¾ Ð¼Ñ–ÑÑ‚Ð¸Ñ‚ÑŒ Ð´Ð¾Ð´Ð°Ð½Ñƒ ÐºÐ½Ð¸Ð³Ñƒ Ð² Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ðµ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ðµ Ð½Ð° Ð¼Ñ–ÑÑ†Ðµ ÑÑ‚Ð°Ñ€Ð¾Ð³Ð¾ Ð¼Ð°ÑÐ¸Ð²Ñƒ
    addDataToStorage(STORAGE_KEY, dataFromStorage);
    // Ð·Ð°Ð¿ÑƒÑÐºÐ°Ñ”Ð¼Ð¾ Ñ„ÑƒÐ½ÐºÑ†Ñ–ÑŽ Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€Ð¸,
    //ÑÐºÐ° Ð·Ð¼Ñ–Ð½Ð¸Ñ‚ÑŒ ÐºÐ°Ñ€Ñ‚ÐºÑƒ Ð¿Ð¾Ñ‚Ð¾Ñ‡Ð½Ð¾Ñ— ÐºÐ½Ð¸Ð³Ð¸,
    //Ð½Ð° ÐºÐ°Ñ€Ñ‚ÐºÑƒ ÐºÐ½Ð¸Ð³Ð¸, ÑÐºÐ° Ð²Ð¶Ðµ Ñ” Ð² ÑÐ¿Ð¸ÑÐºÑƒ Ð´Ð¾Ð´Ð°Ð½Ð¸Ñ… ÐºÐ½Ð¸Ð³
    checkingBookList(newBook);
  } catch (error) {
    console.log(error.message);
    // Notify.failure('Oops! Something went wrong! Try reloading the page!');
  }
}

// Ð¤ÑƒÐ½ÐºÑ†Ñ–Ñ Ð²Ð¸Ð´Ð°Ð»ÐµÐ½Ð½Ñ Ð¾Ð±Ñ”ÐºÑ‚Ð° ÐºÐ½Ð¸Ð³Ð¸ Ð· Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ð¾Ð³Ð¾ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ð°
async function removeBookFromStorage() {
  try {
    // Ð¾Ñ‚Ñ€Ð¸Ð¼Ð°Ð½Ð½Ñ Ð¼Ð°ÑÐ¸Ð²Ñƒ Ð¾Ð±Ñ”ÐºÑ‚Ñ–Ð² ÐºÐ½Ð¸Ð³, Ñ‰Ð¾ Ð²Ð¶Ðµ Ð·Ð±ÐµÑ€Ñ–Ð³Ð°Ñ”Ñ‚ÑŒÑÑ Ð² Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ð¾Ð¼Ñƒ ÑÑ…Ð¾Ð²Ð¸Ñ‰Ñ–
    const dataFromStorage = await getDataFromStorage(STORAGE_KEY);
    // ÑÑ‚Ð²Ð¾Ñ€ÐµÐ½Ð½Ñ Ð½Ð¾Ð²Ð¾Ð³Ð¾ Ð¼Ð°ÑÐ¸Ð²Ñƒ Ð² ÑÐºÐ¾Ð¼Ñƒ Ð½Ðµ Ð±ÑƒÐ´Ðµ Ð¾Ð±Ñ”ÐºÑ‚Ð° Ð¿Ð¾Ñ‚Ð¾Ñ‡Ð½Ð¾Ñ— ÐºÐ½Ð¸Ð³Ð¸
    const clearStorage = dataFromStorage.filter(
      book => book._id !== newBook._id
    );
    // Ð¿Ð¾Ð¼Ñ–Ñ‰Ð°Ñ”Ð¼Ð¾ Ð½Ð¾Ð²Ð¸Ð¹ Ð¼Ð°ÑÐ¸Ð², ÑÐºÐ¸Ð¹ Ð²Ð¶Ðµ Ð½Ðµ Ð¼Ñ–ÑÑ‚Ð¸Ñ‚ÑŒ Ð¿Ð¾Ñ‚Ð¾Ñ‡Ð½Ð¾Ñ— ÐºÐ½Ð¸Ð³Ð¸ Ð² ÑÑ…Ñ€Ð¾Ð²Ð¸Ñ‰Ðµ Ð·Ð°Ð¼Ñ–ÑÑ‚ÑŒ ÑÑ‚Ð°Ñ€Ð¾Ð³Ð¾
    addDataToStorage(STORAGE_KEY, clearStorage);
    // Ð·Ð°Ð¿ÑƒÑÐºÐ°Ñ”Ð¼Ð¾ Ñ„ÑƒÐ½ÐºÑ†Ñ–ÑŽ Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€Ð¸,
    //ÑÐºÐ° Ð·Ð¼Ñ–Ð½Ð¸Ñ‚ÑŒ ÐºÐ°Ñ€Ñ‚ÐºÑƒ Ð¿Ð¾Ñ‚Ð¾Ñ‡Ð½Ð¾Ñ— ÐºÐ½Ð¸Ð³Ð¸,
    //Ð½Ð° ÐºÐ°Ñ€Ñ‚ÐºÑƒ ÐºÐ½Ð¸Ð³Ð¸, ÑÐºÐ¾Ñ— Ñ‰Ðµ Ð½ÐµÐ¼Ð°Ñ” Ð² ÑÐ¿Ð¸ÑÐºÑƒ Ð´Ð¾Ð´Ð°Ð½Ð¸Ñ… ÐºÐ½Ð¸Ð³
    checkingBookList(newBook);
  } catch (error) {
    console.log(error.message);
    // Notify.failure('Oops! Something went wrong! Try reloading the page!');
  }
}
